import Header from './Components/Header';
import { Image } from 'react-bootstrap';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import logo from './logo.gif'
import Characters from './Components/Characters';
import Goods from './Components/Goods';
import Delivery from './Components/Delivery';
import Contacts from './Components/Contacts';
import Lucy from './Components/Lucy';
import Misa from './Components/Misa';
import Mai from './Components/Mai';
import { useState, useEffect} from 'react';
import { ClipLoader } from 'react-spinners';
import { Canvas } from '@react-three/fiber';
import { Model } from './Components/Model';

function App() {
  const [loading, setLoading] = useState(false)
  useEffect(() => {
    setLoading(true)
    setTimeout(() => {
      setLoading(false)
    }, 1000)
  }, [])
  return (
    <div className='App'>
      {
        loading ?
          <div className='d-flex justify-content-center align-items-center loader'>
            <ClipLoader color={'#faee51'} loading={loading} size={150} />
          </div>
          :
          <div>
            <div className="bg">
              <Header></Header>
              {/* <Canvas >
                <ambientLight intensity={0.1} />
                <directionalLight position={[1, 1, 1]} intensity={0.8} /> 
                <Model scale={0.125} />         
              </Canvas> */}
              <div className='w-50 text-center mx-auto d-flex align-items-center h-100'>
                <Image fluid
                  src={logo}
                />
              </div>
            </div>
            <Characters></Characters>
            <Goods></Goods>
            <Misa></Misa>
            <Lucy></Lucy>
            <Mai></Mai>
            <Delivery></Delivery>
            <Contacts></Contacts>
          </div>
      }
    </div>
  );
}

export default App;
